.van-index-bar {
  position: relative;
}
.van-index-bar__sidebar {
  position: fixed;
  top: 50%;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-user-select: none;
          user-select: none;
  z-index: 9999;
}
.van-index-bar__index {
  font-weight: 500;
  padding: 0 0.17067rem 0 0.68267rem;
  padding: 0 var(--padding-base, 0.17067rem) 0 var(--padding-md, 0.68267rem);
  font-size: 0.42667rem;
  font-size: var(--index-bar-index-font-size, 0.42667rem);
  line-height: 0.59733rem;
  line-height: var(--index-bar-index-line-height, 0.59733rem);
}
.van-index-bar__popup {
  width: 70vw;
}
